.modal{
    height: 100%;
    width: 100%;
    background-color: #61616199;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    z-index: 999;
}
.modal.active{
    opacity: 1;
    pointer-events: all;
}
.modal-content{
    border-radius: 20px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    max-width: 280px;
}
.modal-content.active{
    transform: scale(1);
}
.modal-close-button {
    position: absolute;
    top: 16px;
    right: 16px;
    width: 24px;
    height: 24px;
    padding: 0;
    background-color: transparent;
    color: #54ADFF;
    border: none;
    cursor: pointer;
  }
.modal-close-button svg {
    width: 24px;
    height: 24px;
}
.modal-close-button:hover{
    color: #FFC107;
}

@media screen and (min-width: 768px){
    .modal-content{
        border-radius: 40px;
        max-width: 681px;
    }
    .modal-close-button {
        top: 24px;
        right: 24px;
    }
}